
var myDate = new Date();
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState} from "vuex";
export default {
name: "Topic",
props:['id'],
data(){
return{
topic:"",
repyt:"",//
isshowp:false,  //
reply_id:"",  //id
}
},
computed:{
...mapState(['isLogined'])
},
components:{
CellBox,
Group,
Flexbox,
FlexboxItem,
Card,
XTextarea,
XButton,
Popup
},
directives:{
TransferDom
},


methods:{

giveClick(id){
var app = this
this.$http.post("/reply/"+id+"/ups",{
accesstoken:this.$store.state.accesstoken,
}).then(function (res) {
console.log(res.data)

if (res.data.success){   //

for(var i =0 ; i<app.topic.replies.length;i++){  //
if (app.topic.replies[i].id==id){   //id  reply
reply = app.topic.replies[i]
break;
}
}

var reply;
if (res.data.action=="up"){   //

reply.ups.push(app.$store.state.author_id)
}
if (res.data.action=="down"){  //

var index=  reply.ups.indexOf(app.$store.state.author_id)  
reply.ups.splice(index,1)  
}

}
})
},


collectButton(){
var app =this
this.$http.post("/topic_collect/collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function (res) {
console.log(res)
if (res.data.success){
app.topic.is_collect = true
}
})
},

del_collect(){
var app =this
this.$http.post("/topic_collect/de_collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function (res) {
console.log(res)
if (res.data.success){
app.topic.is_collect = false
}
})
},
hanlep:function(reply_id,replY_name) {  //
this.isshowp=true;
this.reply_id = reply_id      
this.repyt ="@"+replY_name +" "+this.repyt   
console.log(reply_id)
},

hidePopup:function(){
this.reply_id="";
this.repyt="";
},

addReply:function (Type) {   //
var app = this;
console.log("aaaa")
var params={   //  
accesstoken : this.$store.state.accesstoken,
content:this.repyt
}

if (Type == 2) {   //paramsreply_id
params.reply_id= this.reply_id
}

this.$http.post("/topic/"+this.topic.id+"/replies ",params).then(function (res) {
console.log(res)
if(res.data.success){   //
var reply = {   // reply
"id": res.data.reply_id ,  //id
"author": {   //
"loginname": app.$store.state.loginname,
"avatar_url":app.topic.author.avatar_url,
},
"content": params.content,   //
"ups": [],
"create_at": myDate.toLocaleString( ),   //
"reply_id": null,
"is_uped": false

}
app.topic.replies.push(reply);   
app.isshowp=false
app.repyt="";  
}
})
}
},
beforeRouteEnter(to,from,next){
next(function (vm) {
vm.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function (res) {
console.log(res);
if (res.data.success){
vm.topic = res.data.data
}

})
})
},
beforeRouteUpdate(to,from,next){
this.$http.get('/topic/'+to.params.id,{
accesstoken:this.$state.state.accesstoken
}).then(function (res) {
console.log(res);
this.topic = res.data.data
})
next()
}

}



