
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
name: "Create",

data(){
return{
title:"",  //
content:"",  //

toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
strikethrough: true, // 
quote: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
preview: true, // 
}
}

},
components:{
XTextarea,
Group,
XButton,
mavonEditor
},
methods:{
hantopic:function () {
var app=this
console.log("aaaaa")
this.$http.post("/topics",{
accesstoken:this.$store.state.accesstoken,  //
tab:"dev",  //
title:this.title,  //
content:this.content 
}).then(function (res) {
console.log(res)
if (res.data.success){   //
app.$router.push("/topiclist/dev")
}
})
}

}
}



