
import {mapMutations,mapActions} from "vuex"
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: "Longins",
data(){
return{
login: true,
accessToken:"",
isStoast:false,        toastext:""   //
}
},
components: {
Group,
XInput,
XButton,
Toast
},
methods:{
...mapMutations(['mutationsLoined','mutationsLonginnema','mutationsAccess','mutationsAutId','']),
...mapActions(['gettersCount']),
doLinp(){
var app = this
this.$http.post("/accesstoken",{
accesstoken: this.accessToken
}).then(function (res) {
console.log(res.data)
if (res.data.success){
app.login=false
app.mutationsLonginnema(res.data.loginname);  
app.mutationsAccess(app.accessToken)  
app.mutationsAutId(res.data.id)


app.mutationsLoined()

window.localStorage.setItem("loingStateus",true);
window.localStorage.setItem("author_id",res.data.id);
window.localStorage.setItem("name",res.data.loginname);
window.localStorage.setItem("accesstoken",app.accessToken)
app.gettersCount()

app.$router.push("/user/"+res.data.loginname)

}
}).catch(function (err) {
console.log(err.response)
app.isStoast=true;
app.toastext = err.response.data.error_msg
})
}
},
}



