
import {mapMutations} from "vuex"
import Msg from 'vux/src/components/msg/index.vue'

export default {
name: "logout",
data(){
return{
buttons:[{
text:"",
type:"primary",
link:"/login"

}]
}
},
components: {
Msg
},
methods:{
...mapMutations(['mutationsUserinof','mutationsmsgs']),
},
created() {

if (this.$store.state.isLogined==false){
return
}

localStorage.removeItem("accesstoken")
localStorage.removeItem("loingStateus")
localStorage.removeItem("name")
this.mutationsUserinof()   
this.$router.push("/topiclist/all")
this.mutationsmsgs(0)   
},

}



