
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import {mapActions} from "vuex"
export default {
name: "Messages",
data(){
return{
selectedIndex:0,  //
hasReadMsgs:"",  //
hsaNotReadMsgs:""  
}
},
components: {
ButtonTab,
ButtonTabItem,
Cell,
Group,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
methods:{
...mapActions(['gettersCount']),

macAll(){
var app = this
this.$http.post("/message/mark_all ",{
accesstoken:this.$store.state.accesstoken
}).then(function (res) {
console.log(res.data)
if (res.data && res.data.success){
app.$router.go(0)
}
})
},

onButtonClick(id){
var app = this
this.$http.post("/message/mark_one/"+id,{
accesstoken:this.$store.state.accesstoken
}).then(function (res) {
console.log(res)
if (res.data && res.data.success){
app.gettersCount()
console.log("")

var markmsgs;
for (var i = 0; i <app.hsaNotReadMsgs.length ; i++) {
if (app.hsaNotReadMsgs[i].id == id){
markmsgs = app.hsaNotReadMsgs[i]
app.hsaNotReadMsgs.splice(i,1)
break
}
}
app.hasReadMsgs.unshift(markmsgs)

}
})
},
getReadMsgs(){  //
var app = this
this.$http.get("/messages",{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function (res) {
console.log(res.data.data)
if (res.data && res.data.success ){
app.hsaNotReadMsgs = res.data.data.hasnot_read_messages  
app.hasReadMsgs = res.data.data.has_read_messages  

}
})
},

handleTab(index){
console.log(index)
this.selectedIndex=index
},
},
created() {
this.getReadMsgs()
}
}



