import Vue from "vue";
import Vuex from "vuex"
import mutations from "./mutations";
import actions from "./actions";
Vue.use(Vuex);
const  store = new Vuex.Store({

  state:{
    topiclist:[
      { name:"all",desc:""},
      { name:"good",desc:""},
      { name:"share",desc:""},
      { name:"ask",desc:""},
      { name:"job",desc:"",},
      { name:"dev",desc:"",},
    ],
    issCon:false,   //actionsheet
    isLogined:false,  //
    loginedMenus: {
      create:"",
      center:"",
      msgs:"",
      logout:"",
    },
    nulogunMenus:{
      login:""
    },
    loginname:"",  //
    accesstoken:"", //
    author_id:"",  //id
    msgsCount:"",  //

  },
  getters:{
    moreMenus(state){    //11
      if (state.isLogined){    //
        return state.loginedMenus   //
      }
      return state.nulogunMenus    //
    },
  //  
    fileterIab(state){
        return function (tab) {
          for (var i = 0; i <state.topiclist.length ; i++) {
              const element = state.topiclist[i]
            if (element.name==tab){
                return state.topiclist[i].desc
            }
          }
        }
    }
  },
  mutations,
  actions,



});

export  default store;



