
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor'
import axios from "axios"
import 'mavon-editor/dist/css/index.css'
export default {
name: "Edit",
props:['id'],
data(){
return{
topic:{
title:"",
content:"",
}
}
},
components:{
XTextarea,
Group,
XButton,
mavonEditor
},
methods:{

upload(pos,file){   //posmarkdown  fileFile

console.log(pos)
console.log(file)
var params=new FormData();
3.



axios.post("https://sm.ms/api/v2/").then(function (res) {
console.log(res)
})
},

edittopic:function () {
var app=this
this.$http.post("/topics/update ",{
accesstoken:this.$store.state.accesstoken,  //
topic_id:this.topic.id,   //id
tab:"dev",  //
title:this.topic.title,  //
content:this.topic.content 
}).then(function (res) {
console.log(res)
if (res.data.success){   //
app.$router.back(); 
}
})
}

},

beforeRouteEnter(to,from,next){
next(function (vm) {
vm.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function (res) {
console.log(res);
if (res.data.success){
vm.topic = res.data.data
}

})
})
},
beforeRouteUpdate(to,from,next){
var app = this;
this.$http.get('/topic/'+to.params.id,{
accesstoken:this.$state.state.accesstoken
}).then(function (res) {
console.log(res);
app.topic = res.data.data
})
next()
}

}



