import request from "../ajaxUrl/ajaxUrl";
const actions={
  //
  gettersCount(context){

    return  request.get("/message/count?accesstoken=" + context.state.accesstoken).then(function (res) {
      console.log(res)
      if (res.data  && res.data.success ){
        // app.msgCount=res.data.data;
        context.commit("mutationsmsgs",res.data.data)

      }
    })
  },
}
export default actions



