
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {mapGetters,mapMutations,mapActions,mapState} from 'vuex'
import VmBackTop from "vue-multiple-back-top"
export default {
name: 'app',
data(){
return{
msgCount:"",

}
},

components: {
XHeader,
Actionsheet,
VmBackTop,
Badge,

},

methods:{    //
...mapMutations(['mutationsLoined','mutationsLonginnema','mutationsAccess','mutationsAutId']),
...mapActions(['gettersCount']),
showMore(){   //
this.issCon = true
},
shoTitle(){  //
this.$router.push("/topiclist/all")

},
handleActions(menuKey){
this.$router.push('/'+menuKey)
}

},
computed:{     //
...mapState(['msgsCount']),
issCon:{

get(){ return this.$store.state.issCon },
set(nval){ return this.$store.commit("mutationsIsShow",nval) },
},
...mapGetters(['moreMenus']),


},

created() {


var loingStateus= window.localStorage.getItem("loingStateus")
if (loingStateus){  //true
this.mutationsLoined()  
this.mutationsLonginnema(window.localStorage.getItem('name'));  
this.mutationsAccess(window.localStorage.getItem('accesstoken'))  
this.mutationsAutId(window.localStorage.getItem("author_id"))

this.gettersCount();
var app = this
window.setInterval(function () {    //5ajax app.gettersCount();
app.gettersCount();
},1000*60*5)
}

}

}



