
import Card from 'vux/src/components/card/index.vue'

import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name: "User",
data(){
return{
userIname:"",
collectInfo:""
}
},
props:['loginname'],
components:{
Card,
Cell, Group, Scroller
},
beforeRouteEnter(to,from,next){
next(function (vm) {
vm.$http.get("/USER/"+to.params.loginname).then(function (res) {
console.log(res.data.data)
vm.userIname = res.data.data
});

vm.$http.get("/topic_collect/"+to.params.loginname).then(function (res) {
console.log(res.data)
vm.collectInfo = res.data.data
})
})

},
beforeRouteUpdate(to,from,next){
var app = this
this.$http.get("/USER/"+to.params.loginname).then(function (res) {
console.log(res.data)
app.userIname= res.data.data
});
this.$http.get("/topic_collect/"+to.params.loginname).then(function (res) {   //
console.log(res.data)
app.collectInfo = res.data.data
});

next()
}
}



