
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState, mapGetters} from "vuex"
export default {
name: "Topiclist",
props:['name'],
data(){
return{
listdata:[],
page:1,   //1
limit:20,  //
pulldownConfig:{
content:"",
downContent:"",
upContent:"",
loadingContent:"..."
},
pullUpConfis:{
content:"",
downContent:"",
upContent:"",
loadingContent:"..."
}
}
},
components: {
Cell,
Group,
Scroller,
XInput,
XButton,
Tab,
TabItem
},
computed:{
...mapState(['topiclist']),//state
...mapGetters(['fileterIab'])  //
},
methods:{
clickTab(index){    //
this.$router.push("/topiclist/"+this.topiclist[index].name)
},

pulldownLoading(){
var app = this
this.$http.get("/topics",{
params:{
tab:this.name,
page:1,
limit:this.limit,
}
}).then(function(res){
console.log(res)
if(res.data.success){
app.listdata=res.data.data
app.$refs.Pullup.donePulldown()   
app.page=1
}
})
},
loadMore(){   //ajax 
var app = this
this.page = this.page+1;
this.$http.get("/topics",{
params:{
tab:this.name,
page:this.page,
limit:this.limit,
}
}).then(function(res){
if(res.data.success){
for (var i = 0; i <res.data.data.length; i++) {
app.listdata.push(res.data.data[i])

}
app.$refs.Pullup.donePullup()   
}
})
}
},


beforeRouteEnter(to,form,next){
next(function(vm){
vm.$http.get("/topics",{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit,
}
}).then(function(res){
vm.listdata=res.data;
if(res.data.success){
vm.listdata=res.data.data
}
})
})
},


beforeRouteUpdate(to,from,next){
var app = this;
app.$http.get("/topics",{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit,
}
}).then(function (response) {
console.log(response);
app.listdata=response.data;
if (response.data.success){
app.listdata = response.data.data;
}

})
next();
}
}



