import Vue from 'vue'
import VueRouter from "vue-router";
import Home from  '../components/Home'
import Topiclist from "../components/Topiclist";
import Topic from "../components/Topic";
import login from "../components/login";
import center from "../components/center";
import msgs from "../components/msgs";
import logout from "../components/logout";
import user from  "../components/User"
import Create from "../components/Create";
import Collect from "../components/Collect";
import Edit from "../components/Edit";


Vue.use(VueRouter);
const  routes=[
  // {
  // path:"/",
  // component:Home
  // },
  {  //  
    path:'/',
    redirect:'/topiclist/all',
  },
  {
    path: '/topiclist/:name',   //  
    name:"topiclist",
    props:true,
    component: Topiclist
  },
  {
    path: '/topic/:id',     // (id)
    name: "topic",
    props: true,
    component: Topic
  },
  {   //
    path: '/login',
    name: 'login',
    component: login
  },
  {   //
    path: '/center',
    name: 'center',
    component: center
  },
  {  //
    path: '/msgs',
    name: 'msgs',
    component: msgs
  },
  {   //
    path: '/logout',
    name: 'logout',
    component: logout
  },
  {   //
    path: '/user/:loginname',
    name: 'user',
    props: true,
    component: user
  },
  {    //
    path: "/create",
    name: "create",
    component: Create,
  },
  //
  {
    path: "/collect",
    component: Collect,
  },
//  
  {
    path: "/topic/:id/edit",
    name: "edit",
    component: Edit,
    props: true
  },


]

const  router = new  VueRouter({
  routes,
})
export  default  router;



